// FeedItems are found in the dashboard home page. They are populated via an ajax call. They should in the end have
// the same markup as the summary table component. In the meantime, this stylesheet makes a FeedItem look like a
// summary table component.

.Feed {
    padding-top: $spacer / 2;
}

.FeedItem {
    position: relative;
    padding: 12px 0;
    border-bottom: $table-border-width solid $table-border-color;

    &:hover {
        background-color: #f9fdfe;
    }

    &:first-child {
        border-top: $table-border-width solid #ddd;
    }

    h2 {
        padding: 0;
        margin-top: -3px;
        margin-left: 0;
        margin-right: 0;
        border-top: 0;
        border-bottom: 0;
        font-weight: 400;
        font-size: 13px;
        text-overflow: ellipsis;
        margin-bottom: 2px;
        line-height: $line-height-lg;

        a {
            @include reverse-link();
        }
    }

    .FeedDescription {
        @extend .info;
        margin-bottom: -2px;
        font-weight: 300;

        strong {
            font-weight: 400;
        }

        p {
            margin-bottom: 0;
            display: inline-block;
        }

        img {
            display: none;
        }

        .details p {
            margin-bottom: 0;
            display: initial;
            line-height: 1.4;
        }

        .read-less {
            display: block;
            margin-top: 3px;
        }
    }

    .FeedDescription,
    h2 {
        margin-right: 102px;
        margin-left: 12px;
    }

    .Date {
        @extend .info;

        position: absolute;
        right: 12px;
        width: 102px;
        line-height: 1;
        top: $spacer * 0.75 + (30px / 2) - ($font-size-tables / 2);
        text-align: right;
        font-weight: 300;
    }
}
